	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"We spend so much time building things that we sometimes forget that we're building for, and with, other complex human beings. Remembering that we are people first is the first step to making a positive shift in the way we treat each other.\n\nhttp://2014.jsconf.us/","meta":{"duration":"PT19M45S"},"presenter":{"name":null},"source":{"id":"LlO2_GecWo8","provider":"youtube"},"slug":"brian-j-brennan-being-human-jsconf2014","tag":["JSConf","JSConf2014","Brian J Brennan"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/LlO2_GecWo8/default.jpg","height":90,"width":120}},"title":"Brian J Brennan: Being Human [JSConf2014]"}},{"node":{"description":"To most people in JS, functional programmers are perceived as academic hipsters raving about things like applicative functors, semigroup homomorphisms and Yoneda lemmas for no good reason except to make the rest of us feel stupid. And this is fair; there's no better way to make you feel pitifully mainstream than throwing category theory at you. Conversely, JS programmers tend to believe functional programming, therefore, can have no real world application because nobody in the real world has any idea what a Yoneda lemma is and they seem to be getting by just fine without it.\n\nExcept we aren't. We've been living in callback hell for almost two decades now, and no matter how many control flow libraries we submit to npm, things don't seem to be getting any better. And that's where functional programming comes in—turns out callbacks are just functions, and those academics in their ivory towers with their Haskell compilers actually encountered and solved these problems long ago. And now we can have their solutions in JS too, because of functional reactive programming. To demonstrate, I'll attempt to write a browser based game, from scratch, with ponies, using RxJS, everybody's favourite reactive library, live on stage in 30 minutes with no callback hell in sight. And we'll be finding out if this reactive stuff is all it's cracked up to be or not.\n\nhttp://2014.jsconf.us/","meta":{"duration":"PT31M6S"},"presenter":{"name":null},"source":{"id":"x8mmAu7ZR9Y","provider":"youtube"},"slug":"bodil-stokke-reactive-game-development-for-the-discerning-hipster-jsconf2014","tag":["JSConf","JSConf2014","Bodil Stokke"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/x8mmAu7ZR9Y/default.jpg","height":90,"width":120}},"title":"Bodil Stokke: Reactive Game Development For The Discerning Hipster [JSConf2014]"}}]}},"pathContext":{"slug":"jsconf2014","tag":"JSConf2014"}}

